<template>
<div id="enterpriseRight">
  <div id="right">
      <div id="enterpriseHot">
        <div class="more"></div>
        <div id="hotTitle">
          <router-link to="/enterprise" v-for="i in enterpriseHot" class="gray" :key="i"><br>{{i}}<br></router-link>
        </div>
      </div>
      <div id="enterpriseMine">
        <div id="mineHeader">
          <div class="flexbox2">
            <span class="cursordefault" v-bind:class="{chosenRed:(mine==1)}" @mouseover="changeMine(1,$event)"><i class="fa fa-user" aria-hidden="true"></i>  </span>
            <span class="cursordefault" v-bind:class="{chosenRed:(mine==2)}" @mouseover="changeMine(2,$event)"><i class="fa fa-file-text" aria-hidden="true"></i>  </span>
            <span class="cursordefault" v-bind:class="{chosenRed:(mine==3)}" @mouseover="changeMine(3,$event)"><i class="fa fa-check-square-o" aria-hidden="true"></i>  </span>
          </div>
          <div v-if="mine==1" id="userCenter">userCenter</div>
          <div v-if="mine==2" id="takeNews">takeNews</div>
          <div v-if="mine==3" id="myDeal">myDeal</div>
        </div>
      </div>
      <div id="enterpriseOthers">
        <div id="otherHeader">
          <div class="flexbox2">
            <span class="cursordefault" v-bind:class="{chosenRed:(others==1)}" @mouseover="changeOthers(1,$event)"><i class="fa fa-user" aria-hidden="true"></i>  </span>
            <span class="cursordefault" v-bind:class="{chosenRed:(others==2)}" @mouseover="changeOthers(2,$event)"><i class="fa fa-file-text" aria-hidden="true"></i>  </span>
            <span class="cursordefault" v-bind:class="{chosenRed:(others==3)}" @mouseover="changeOthers(3,$event)"><i class="fa fa-check-square-o" aria-hidden="true"></i>  </span>
          </div>
          <div v-if="others==1" id="recentActive">recentActive</div>
          <div v-if="others==2" id="communityActive">communityActive</div>
          <div v-if="others==3" id="other">other</div>
        </div>
      </div>
      <div id="advertisePlace">
        
      </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'enterpriseRight',
  data(){
    return {
      newsPics:["./static/PICS/timg5.jpg","./static/PICS/timg5.jpg"],
      enterpriseHot:["",
        "20181/5",
        "1100",
        "2018"
        ],
      mine:1,
      others:1,
      rightShow:0,
    }
  },
  methods:{
    changeMine:function(n,event){
      this.mine=n;
      //event.target.style.color='red';
    },
    changeOthers:function(n,event){
      this.others=n;
    }
  }
}
</script>
<style>
</style>



